.contact {
  background: var(--bg-primary);
  padding-bottom: 0;
}

.contact-subtitle {
  text-align: center;
  color: var(--text-secondary);
  font-size: 1.1rem;
  line-height: 1.6;
  max-width: 600px;
  margin: 0 auto 3rem;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 3rem;
  align-items: start;
}

.contact-info h3,
.contact-form-container h3 {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 2rem;
}

.contact-methods {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
}

.contact-method {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: var(--bg-card);
  border-radius: 0.75rem;
  border: 1px solid var(--border-color);
  text-decoration: none;
  transition: all 0.3s ease;
}

.contact-method:hover {
  box-shadow: var(--shadow);
  transform: translateX(5px);
}

.contact-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  background: linear-gradient(135deg, var(--primary-color), var(--accent-color));
  border-radius: 50%;
  color: white;
  font-size: 1.2rem;
}

.contact-details {
  display: flex;
  flex-direction: column;
}

.contact-title {
  font-weight: 500;
  color: var(--text-primary);
  font-size: 0.9rem;
}

.contact-value {
  color: var(--text-secondary);
  font-size: 1rem;
}

.social-section {
  margin-bottom: 2rem;
}

.social-section h4 {
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.social-links {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  background: var(--bg-card);
  border-radius: 50%;
  border: 1px solid var(--border-color);
  text-decoration: none;
  color: var(--text-primary);
  transition: all 0.3s ease;
  font-size: 1.2rem;
}

.social-link:hover {
  background: var(--social-color, var(--primary-color));
  color: white;
  box-shadow: var(--shadow);
}

.availability {
  background: var(--bg-card);
  border-radius: 0.75rem;
  padding: 1.5rem;
  border: 1px solid var(--border-color);
}

.availability-status {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.status-indicator {
  width: 8px;
  height: 8px;
  background: #10b981;
  border-radius: 50%;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.availability-status span {
  font-weight: 500;
  color: var(--text-primary);
}

.availability p {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin: 0;
}

.contact-form {
  background: var(--bg-card);
  border-radius: 1rem;
  padding: 2rem;
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow);
}

.form-group {
  margin-bottom: 1.5rem;
}

.input-group {
  position: relative;
}

.input-icon {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: var(--text-secondary);
  font-size: 1rem;
  z-index: 1;
}

.textarea-icon {
  top: 1rem;
  transform: none;
}

.input-group input,
.input-group textarea {
  width: 100%;
  padding: 1rem 1rem 1rem 3rem;
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  background: var(--bg-secondary);
  color: var(--text-primary);
  font-size: 1rem;
  transition: all 0.3s ease;
  resize: vertical;
}

.input-group input:focus,
.input-group textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.input-group input::placeholder,
.input-group textarea::placeholder {
  color: var(--text-secondary);
}

.submit-btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 1rem;
  background: linear-gradient(135deg, var(--primary-color), var(--secondary-color));
  color: white;
  border: none;
  border-radius: 0.5rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-btn:hover {
  box-shadow: var(--shadow-lg);
}

.footer {
  background: var(--bg-secondary);
  border-top: 1px solid var(--border-color);
  padding: 2rem 0;
  margin-top: 3rem;
}

.footer-content {
  text-align: center;
  color: var(--text-secondary);
}

.footer-content p {
  margin: 0.25rem 0;
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .contact-method {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }
  
  .contact-method:hover {
    transform: translateY(-3px);
  }
  
  .social-links {
    flex-direction: row;
    justify-content: center;
    gap: 1rem;
  }
  
  .social-link {
    width: 2.5rem;
    height: 2.5rem;
    font-size: 1rem;
  }
  
  .contact-form {
    padding: 1.5rem;
  }
}