.hero {
  min-height: calc(100vh - 70px);
  display: flex;
  align-items: center;
  position: relative;
  background: linear-gradient(135deg, var(--bg-primary) 0%, var(--bg-secondary) 100%);
  overflow: hidden;
  padding-top: 0;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><defs><pattern id="grid" width="10" height="10" patternUnits="userSpaceOnUse"><path d="M 10 0 L 0 0 0 10" fill="none" stroke="%23e5e7eb" stroke-width="0.5" opacity="0.3"/></pattern></defs><rect width="100" height="100" fill="url(%23grid)"/></svg>');
  opacity: 0.5;
}

.hero-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  position: relative;
  z-index: 1;
}

.hero-title {
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.hero-subtitle {
  font-size: 1.5rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
}

.hero-description {
  font-size: 1.1rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 2rem;
}

.hero-stats {
  display: flex;
  gap: 2rem;
  margin-bottom: 2rem;
}

.stat {
  text-align: center;
}

.stat-number {
  display: block;
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary-color);
}

.stat-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.hero-actions {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
}

.social-links {
  display: flex;
  gap: 1rem;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: 50%;
  color: var(--text-primary);
  text-decoration: none;
  transition: all 0.3s ease;
}

.social-link:hover {
  background: var(--primary-color);
  color: white;
  transform: translateY(-2px);
}

.hero-visual {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.profile-container {
  position: relative;
}

.profile-image {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--primary-color), var(--accent-color));
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-lg);
  position: relative;
  overflow: hidden;
  padding: 4px;
}

.profile-photo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
  object-position: center top;
}

.profile-placeholder {
  font-size: 4rem;
  font-weight: 700;
  color: white;
}

.floating-elements {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.floating-tech {
  position: absolute;
  background: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: 0.5rem;
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--text-primary);
  box-shadow: var(--shadow);
}

.floating-tech:nth-child(1) {
  top: 20%;
  left: -20%;
  animation: float 4s ease-in-out infinite;
}

.floating-tech:nth-child(2) {
  top: 60%;
  right: -20%;
  animation: float 4s ease-in-out infinite 1s;
}

.floating-tech:nth-child(3) {
  bottom: 20%;
  left: -15%;
  animation: float 4s ease-in-out infinite 2s;
}

.floating-tech:nth-child(4) {
  bottom: 60%;
  right: -15%;
  animation: float 4s ease-in-out infinite 3s;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

.scroll-indicator {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.scroll-line {
  width: 2px;
  height: 30px;
  background: var(--primary-color);
  border-radius: 1px;
}

@media (max-width: 768px) {
  .hero {
    padding-top: 80px;
    min-height: calc(100vh - 80px);
  }
  
  .hero-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
    padding-top: 2rem;
  }
  
  .hero-title {
    font-size: 2rem;
    line-height: 1.1;
    margin-bottom: 0.5rem;
  }
  
  .hero-subtitle {
    font-size: 1.1rem;
    margin-bottom: 1rem;
  }
  
  .hero-description {
    font-size: 1rem;
    margin-bottom: 1.5rem;
  }
  
  .hero-stats {
    justify-content: center;
    gap: 1.5rem;
    margin-bottom: 1.5rem;
  }
  
  .stat-number {
    font-size: 1.5rem;
  }
  
  .hero-actions {
    flex-direction: column;
    align-items: center;
    gap: 0.75rem;
  }
  
  .btn {
    width: 100%;
    max-width: 250px;
  }
  
  .profile-image {
    width: 200px;
    height: 200px;
  }
  
  .floating-tech {
    font-size: 0.8rem;
    padding: 0.4rem 0.8rem;
  }
  
  .floating-tech:nth-child(1) {
    top: 10%;
    left: -25%;
  }
  
  .floating-tech:nth-child(2) {
    top: 15%;
    right: -25%;
  }
  
  .floating-tech:nth-child(3) {
    bottom: 15%;
    left: -20%;
  }
  
  .floating-tech:nth-child(4) {
    bottom: 10%;
    right: -20%;
  }
  
  .scroll-indicator {
    bottom: 1rem;
  }
}