.education {
  background: var(--bg-secondary);
}

.education-content {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 3rem;
  align-items: start;
}

.subsection-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 2rem;
  text-align: center;
  position: relative;
}

.subsection-title::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 2px;
  background: var(--primary-color);
  border-radius: 1px;
}

.education-timeline {
  position: relative;
}

.education-timeline::before {
  content: '';
  position: absolute;
  left: 20px;
  top: 60px;
  bottom: 0;
  width: 2px;
  background: var(--primary-color);
}

.education-item {
  position: relative;
  margin-bottom: 2rem;
  padding-left: 60px;
}

.education-marker {
  position: absolute;
  left: 10px;
  top: 0;
  width: 40px;
  height: 40px;
  background: var(--primary-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 1.1rem;
  z-index: 1;
}

.education-content-card {
  background: var(--bg-card);
  border-radius: 1rem;
  padding: 1.5rem;
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow);
  transition: all 0.3s ease;
}

.education-content-card:hover {
  box-shadow: var(--shadow-lg);
}

.education-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.education-degree {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-primary);
  flex: 1;
}

.education-grade {
  background: linear-gradient(135deg, var(--primary-color), var(--accent-color));
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 1rem;
  font-weight: 500;
  font-size: 0.9rem;
}

.education-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.institution-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.institution-name {
  font-weight: 500;
  color: var(--primary-color);
}

.location {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.period {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  color: var(--text-secondary);
  font-size: 0.9rem;
  background: var(--bg-secondary);
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
}

.education-description {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1rem;
}

.achievements h5 {
  color: var(--text-primary);
  margin-bottom: 0.5rem;
  font-size: 0.95rem;
}

.achievements ul {
  list-style: none;
  padding: 0;
}

.achievements li {
  position: relative;
  padding-left: 1.25rem;
  margin-bottom: 0.25rem;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.achievements li::before {
  content: '▸';
  position: absolute;
  left: 0;
  color: var(--primary-color);
  font-weight: bold;
}

.personal-section {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.hobbies-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.hobby-card {
  background: var(--bg-card);
  border-radius: 1rem;
  padding: 1.5rem;
  border: 1px solid var(--border-color);
  text-align: center;
  transition: all 0.3s ease;
}

.hobby-card:hover {
  box-shadow: var(--shadow);
}

.hobby-icon {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.hobby-name {
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.hobby-description {
  font-size: 0.85rem;
  color: var(--text-secondary);
  line-height: 1.4;
}

.personal-details {
  background: var(--bg-card);
  border-radius: 1rem;
  padding: 1.5rem;
  border: 1px solid var(--border-color);
}

.personal-details h4 {
  color: var(--text-primary);
  margin-bottom: 1rem;
  text-align: center;
}

.details-grid {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.detail-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
  border-bottom: 1px solid var(--border-color);
}

.detail-item:last-child {
  border-bottom: none;
}

.detail-label {
  font-weight: 500;
  color: var(--text-primary);
  font-size: 0.9rem;
}

.detail-value {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

@media (max-width: 768px) {
  .education-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .education-timeline::before {
    left: 15px;
  }
  
  .education-item {
    padding-left: 50px;
  }
  
  .education-marker {
    left: 5px;
    width: 30px;
    height: 30px;
    font-size: 1rem;
  }
  
  .education-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .education-meta {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .hobbies-grid {
    grid-template-columns: 1fr;
  }
  
  .detail-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.25rem;
  }
}