.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

[data-theme="dark"] .header {
  background: rgba(17, 24, 39, 0.95);
}

.header.scrolled {
  box-shadow: var(--shadow);
}

.header-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 0;
}

.logo {
  font-size: 1.5rem;
  font-weight: 700;
  cursor: pointer;
}

.nav {
  display: flex;
  gap: 2rem;
}

.nav-link {
  background: none;
  border: none;
  color: var(--text-primary);
  font-weight: 500;
  cursor: pointer;
  padding: 0.5rem 0;
  position: relative;
  transition: all 0.3s ease;
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--primary-color);
  transition: width 0.3s ease;
}

.nav-link:hover::after {
  width: 100%;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.theme-toggle {
  background: none;
  border: none;
  color: var(--text-primary);
  font-size: 1.2rem;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 0.5rem;
  transition: all 0.3s ease;
}

.theme-toggle:hover {
  background: var(--bg-secondary);
}

.mobile-menu-toggle {
  display: none;
  background: none;
  border: none;
  color: var(--text-primary);
  font-size: 1.2rem;
  cursor: pointer;
  padding: 0.5rem;
}

.mobile-nav {
  display: none;
  overflow: hidden;
  background: var(--bg-card);
  border-top: 1px solid var(--border-color);
}

.mobile-nav-link {
  display: block;
  width: 100%;
  background: none;
  border: none;
  color: var(--text-primary);
  font-weight: 500;
  cursor: pointer;
  padding: 1rem 0;
  text-align: left;
  border-bottom: 1px solid var(--border-color);
  transition: all 0.3s ease;
}

.mobile-nav-link:hover {
  background: var(--bg-secondary);
  padding-left: 1rem;
}

@media (max-width: 768px) {
  .header-content {
    padding: 0.75rem 0;
  }
  
  .logo {
    font-size: 1.3rem;
  }
  
  .desktop-nav {
    display: none;
  }
  
  .mobile-menu-toggle {
    display: block;
  }
  
  .mobile-nav {
    display: block;
  }
  
  .mobile-nav.open {
    box-shadow: var(--shadow);
  }
  
  .mobile-nav-link {
    padding: 1rem 1rem;
    font-size: 1rem;
  }
}